header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    padding: 20px;
    border-bottom: 1px solid lightgrey;
    background-color: #fcfcff;
}

.header-left {
    display: block;
}

.header-right {
    display: block;
}

.header-weekday, .header-count-of-tasks, .header-day {
    margin: 0;
}


